import { BrowserRouter, Switch, Route } from "react-router-dom";
import FrontPage from "./components/FrontPage";
import RawDataPage from "./components/RawDataPage";

const App = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/rawData">
          <RawDataPage />
        </Route>
        <Route path="/">
          <FrontPage />
        </Route>
      </Switch>
    </BrowserRouter>
  )
}

export default App;
